@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nova+Square&display=swap);
body{margin:0;padding:0;height:100vh;font-family:'Varela Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


.App{height:auto;overflow:hidden}.App .navigation-margin{height:65px}.App .Content{width:100vw;display:grid;place-items:center;padding-bottom:100px}@media screen and (max-width: 900px){.login-button{margin:0;padding:8px 20px 5px 20px;background-color:white;color:black;border:1px solid transparent;border-radius:10px;font-size:25px;cursor:pointer;transition-duration:0.25s;text-decoration:none}.login-button:hover,.login-button:focus{background:transparent;color:#f8f8f8;border:1px solid #f8f8f8}.logout-button{margin:0;padding:8px 20px 5px 20px;background-color:white;color:black;border:1px solid transparent;border-radius:10px;font-size:25px;cursor:pointer;transition-duration:0.25s;text-decoration:none}}

